.about {
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.about__columns-wrap {
  display: flex;
  justify-content: space-between;
}

.about__map-wrap {
  width: 54.2rem;
  height: 38.8rem;
}

.about__content-wrap {
  position: relative;

  display: flex;
  flex-direction: column;

  width: 47rem;
}

.about__features-list {
  display: flex;
  justify-content: space-between;

  margin-bottom: 3.5rem;

  font-family: @cinema;
  font-size: 2.5rem;
  line-height: 2.5rem;
  letter-spacing: 0.05em;

  color: @dark-brown;
}

.about__feature-item {
  &--center {
    letter-spacing: initial;
  }
  &--big {
    font-family: @droid;
    font-size: 4rem;
    line-height: 4.8rem;
    letter-spacing: initial;
  }
  &:first-child {
    width: 24.5%;
    border-right: 0.2rem solid @dark-brown;
  }
  &:last-child {
    text-align: right;
    width: 30%;
    border-left: 0.2rem solid @dark-brown;
  }
  &:first-child,
  &:last-child {
    // width: 24.5%;
  }
}

.about__title {
  margin-top: auto;
  margin-bottom: auto;

  font-weight: 400;
  font-family: @cinema;
  font-size: 7.5rem;
  line-height: 7.5rem;

  &--absolute {
    position: relative;
    width: 62.5rem;
    // position: absolute;
    // top: 118px;
    left: -15.5rem;

    font-size: 9.5rem;
    text-align: right;
  }
}

.about__links-list {
  display: flex;
  margin-top: auto;

  font-size: 1.5rem;
  line-height: 2.5rem;
}

.about__link-item {
  &:first-child {
    width: 11.5rem;
    margin-right: 9rem;
  }
  &:last-child {
    width: 19rem;
  }
}

.about__desc {
  margin-bottom: 2.2rem;
}

.about__link {
  margin-bottom: 0.5rem;
}

.about__swiper {
  padding-top: 6.5rem;
  padding-bottom: 6.5rem;
}
